/* home.css */
@import url('https://fonts.googleapis.com/css2?family=Allura&family=DM+Serif+Display:ital@0;1&family=Dancing+Script:wght@600&display=swap');
.home-container {
  flex: 2;
  font-family: 'DM Serif Display', serif;
}
.header > h1{
  font-family: 'Allura', cursive;
  background: rgba(20, 64, 59, 0.9);
  color: #fff;
  text-align: center;
  padding: 20px;
  font-size: 40px;
}

.home-main {
  margin:10px;
  height: 60%;
}

.welcome-box {
  width: 60%;
  margin: auto;
  background: rgba(52, 52, 52, 0.2);
  border-radius: 5px;
  box-shadow: 10px 4px 4px 4px rgba(0, 0, 0, 0.6);
  padding: 20px;
  text-align: center;
  /* position: relative; */
  overflow: hidden;
}


.welcome-box h1 {
  
  color: #364e4e;
  font-size: 40px;
  font: optional;
  margin-bottom: 20px;
}
.welcome-box > p{
  margin-top: 37px;
  color: #ddd;
}

.btn {
  margin-top: 13%;  /*change this when the body length increases */
  display: inline-block;
  padding: 20px 80px;
  font-size: 28px;
  text-decoration: none;
  color: #fff;
  background: #14443b;
  border: 1px solid #14443b;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.btn:hover {
  background: #12876f;
}

.footer {
  margin-top: auto;
  text-align: center;
  color: #999;
  background: rgba(20, 64, 59, 0.6);
  padding: 10px;
  border-radius: 0 0 5px 5px;
  position: fixed;
  bottom: 0;
  width: 100%;
}
