/* dash.css */

.teacher-feedback label {
  margin-bottom: 10px;
}

.teacher-feedback > select,
.teacher-feedback > input {
  border: 1px solid #ddd;
  padding: 8px;
  border-radius: 4px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
}

.error-message {
  color: red;
  font-size: 14px;
  text-align: center;
  margin: 10px 0px 15px 0px;
}

.dashboard {
  background-color: rgba(173, 216, 230, 0.3);
  border: none;
  box-shadow: none;
  padding: 30px;
  margin: 0 auto;
  max-width: 90%;
  transition: height 0.5s ease-in-out;
}

.dashboard h1 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  text-align: center;
}

.dashboard label {
  font-size: 16px;
  font-weight: 500;
}

.dashboard button {
  background-color: #14443b;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.dashboard button:hover {
  background-color: #45a049;
}

.checkbox-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.custom-checkbox {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.custom-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.custom-checkbox span {
  position: relative;
  width: 18px;
  height: 18px;
  margin-right: 8px;
  border: 1px solid #14443b;
  border-radius: 3px;
  background-color: #fff;
}

.custom-checkbox input:checked ~ span::before {
  content: '\2713'; /* Unicode check mark character */
  font-size: 14px;
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.custom-checkbox input:checked ~ span {
  background-color: #45a049;
  border: 1px solid #45a049;
  color: grey;
}

.checkbox-wrapper label {
  font-size: 16px;
}

.teacher-feedback button {
  margin: 0 auto;
  display: flex;
}

.dashboard.expanded {
  height: 600px;
}

.dashboard h1.hidden {
  display: none;
}
